import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { useState, useContext } from "react";
import { MyArtsCt, SetDBCt } from "../../App";
import { Button, Select } from "../../styledCpns/Inputs";
import { ActionBtn } from "../CommonCpns";
import TypeFilter from "./TypeFilter";
import { elmtDmgBnes, colorCodes } from "../../data/misc";
import { deepCopy } from "../../helpers/misc";

const stats = [
  "No Priority",
  "ATK",
  "ATK%",
  "DEF",
  "DEF%",
  "HP",
  "HP%",
  "CRIT Rate",
  "CRIT DMG",
  "Elemental Mastery",
  "Energy Recharge",
  "Healing Bonus",
  "Physical DMG Bonus",
  ...elmtDmgBnes
];

const StyledDD = styled.div`
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 2;
  background-color: ${colorCodes.cardBg2};
`;

export default function BtnBar(props) {
  const { setMyArts } = useContext(SetDBCt);
  const [dropped, setDropped] = useState(false);

  const typeFiller = (
    <TypeFilter types={props.types} setTypes={props.setTypes} />
  );

  const selectLine = (
    <SelectLine
      set={props.set}
      setSet={props.setSet}
      stat={props.stat}
      setStat={props.setStat}
    />
  );

  return (
    <div className="btn_bar full-w">
      <Button
        className="mr-3 glow-on-hover"
        variant="positive"
        onClick={() => props.setPrePickerOn(true)}
      >
        Add
      </Button>
      <Button
        className="mr-3 glow-on-hover"
        variant="positive"
        onClick={() =>
          setMyArts((prev) => {
            const newArts = deepCopy(prev);
            newArts.sort((a, b) => sortArtifacts(a, b));
            return newArts;
          })
        }
      >
        Sort
      </Button>
      {window.innerWidth >= 550 && typeFiller}
      {window.innerWidth >= 962 ? (
        selectLine
      ) : (
        <DotBtn dropped={dropped} setDropped={setDropped} />
      )}
      <StyledDD className="full-w">
        <Collapse in={dropped}>
          <div className="px-3">
            {window.innerWidth < 550 && (
              <div className="mt-4">{typeFiller}</div>
            )}
            <div className="mt-4">{selectLine}</div>
          </div>
        </Collapse>
      </StyledDD>
    </div>
  );
}

function DotBtn(props) {
  return (
    <ActionBtn
      className="ml-1"
      icon="fas fa-ellipsis-h"
      bgColor={props.dropped ? "green" : "white"}
      onClick={() => props.setDropped(!props.dropped)}
    />
  );
}

function SelectLine(props) {
  const myArts = useContext(MyArtsCt);
  const cName = (window.innerWidth < 962 ? "mb-4 " : "") + "px-2 pt-2 pb-1";
  return (
    <div className="flex-wrap">
      <div className="mr-3">
        <Select
          className={cName}
          color="white"
          optColor="white"
          bgColor="cardBg3"
          value={props.set}
          onChange={(e) => props.setSet(e.target.value)}
        >
          {["All", ...getSets(myArts)].map((opt, i) => (
            <option key={i}>{opt}</option>
          ))}
        </Select>
      </div>
      <div>
        <Select
          className={cName}
          color="white"
          optColor="white"
          bgColor="cardBg3"
          value={props.stat}
          onChange={(e) => props.setStat(e.target.value)}
        >
          {stats.map((opt, i) => (
            <option key={i}>{opt}</option>
          ))}
        </Select>
      </div>
    </div>
  );
}

function sortArtifacts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  return b.set.localeCompare(a.set);
}

function getSets(arts) {
  const result = [];
  for (let art of arts) {
    if (!result.includes(art.set)) {
      result.push(art.set);
    }
  }
  return result;
}
