import { useState, useContext } from "react";
import { SetDBCt } from "../../App";
import { Box, MyStyledInv } from "../../styledCpns/Layout";
import { Button } from "../../styledCpns/Inputs";
import PrePicker from "../Picker/PrePicker";
import WpPicker from "../Picker/WpPicker";
import LeftSection from "./LeftSection";
import RightSection from "./RightSection";
import weapons from "../../data/weapons";
import { weaponIcons } from "../../data/misc";
import { deepCopy } from "../../helpers/misc";

export default function MyWeapons() {
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [wpType, setWpType] = useState(null);
  const [chosenI, setChosenI] = useState(0);
  const { setMyWps } = useContext(SetDBCt);

  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <MyStyledInv>
        <Box className="btn_bar" height="2.5rem">
          <Button
            className="mr-3 glow-on-hover"
            variant="positive"
            onClick={() => setPrePickerOn(true)}
          >
            Add
          </Button>
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() =>
              setMyWps((prev) => {
                const newWps = deepCopy(prev);
                newWps.sort((a, b) => sortWeapons(a, b));
                return newWps;
              })
            }
          >
            Sort
          </Button>
        </Box>
        <div className="body">
          <LeftSection
            setPrePickerOn={setPrePickerOn}
            chosenI={chosenI}
            setChosenI={setChosenI}
          />
          <RightSection chosenI={chosenI} />
        </div>
      </MyStyledInv>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={weaponIcons}
          pick={(wpType) => {
            setWpType(wpType);
            setPrePickerOn(false);
          }}
        />
      )}
      {wpType && (
        <WpPicker
          needMassAdd={true}
          wpType={wpType}
          close={() => setWpType(null)}
          pick={(newItem) => {
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps.unshift({ ID: Date.now(), user: null, ...newItem });
              return newWps;
            });
          }}
        />
      )}
    </Box>
  );
}

function sortWeapons(a, b) {
  const rA = weapons[a.type][a.name].rarity;
  const rB = weapons[b.type][b.name].rarity;
  if (rA !== rB) {
    const rank = {
      Gold: 5,
      Purple: 4,
      Blue: 3,
      Green: 2,
      Gray: 1
    };
    return rank[rB] - rank[rA];
  }
  const [fA, sA] = a.level.split("/").map((lv) => +lv);
  const [fB, sB] = b.level.split("/").map((lv) => +lv);
  if (fA !== fB) {
    return fB - fA;
  }
  if (a.type !== b.type) {
    const type = {
      Bow: 5,
      Catalyst: 4,
      Polearm: 3,
      Claymore: 2,
      Sword: 1
    };
    return type[b.type] - type[a.type];
  }
  return sB - sA;
}
